import React, { useState, useEffect } from "react";
import "./index.css";
import { AutoComplete, Table, Modal, Button } from "antd";
import HelpModal from "./modals/help-modal";
import StatsModal from "./modals/stats-modal";
import GuessCounter from "./components/guess-counter";

const App: React.FC = () => {
  const [selectedColleagues, setSelectedColleagues] = useState([]);
  const [mysteryColleague, setMysteryColleague] = useState(null);
  const [selectedOption, setSelectedOption] = useState(null);

  const [helpModalVisible, setHelpModalVisible] = useState(false);
  const [statsModalVisible, setStatsModalVisible] = useState(false);
  const [resultModalVisible, setResultModalVisible] = useState(false);

  const [numTries, setNumTries] = useState(0);
  const [guessed, setGuessed] = useState(false);
  const [failed, setFailed] = useState(false);
  const [stats, setStats] = useState([]);
  const [showPlayAgain, setShowPlayAgain] = useState(false);

  const maxGuesses = 3;
  //TODO: Make mystery colleagues not repeating
  const dataset = [
    {
      Name: "Barbora Dilsk",
      Age: "31",
      Project: "iNM",
      Team: ["TST", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Dvid Pida",
      Age: "26",
      Project: "iNM",
      Team: ["DEV", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Duan Tich",
      Age: "30",
      Project: "iNM",
      Team: ["DEV", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Dusan Emrich",
      Age: "33",
      Project: "iNM",
      Team: ["ANALYSIS", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Kristin Sokol",
      Age: "25",
      Project: "iNM",
      Team: ["DEV", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Marco Petrek",
      Age: "25",
      Project: "iNM",
      Team: ["TST", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Martin Bachrat",
      Age: "29",
      Project: "iNM",
      Team: ["DEV", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Martin Mokr",
      Age: "28",
      Project: "iNM",
      Team: ["DEV", "RETRIEVAL"],
      State: "SK",
      Extra: "approxAge"
    },
    {
      Name: "Matej Kuric",
      Age: "35",
      Project: "iNM",
      Team: ["ANALYSIS", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Petra Heribanov",
      Age: "34",
      Project: "iNM",
      Team: ["TST", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Terzia Kazikov",
      Age: "31",
      Project: "iNM",
      Team: ["ANALYSIS", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Tom Frala",
      Age: "35",
      Project: "iNM",
      Team: ["DEV", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Vojtech Holenia",
      Age: "38",
      Project: "iNM",
      Team: ["ARCH", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Zdeno Franek",
      Age: "37",
      Project: "iNM",
      Team: ["DEV", "RETRIEVAL"],
      State: "SK"
    },
    {
      Name: "Janka M.",
      Age: "33",
      Project: "iNM",
      Team: ["TST", "VAV"],
      State: "SK",
      Extra: "approxAge"
    },
    {
      Name: "Anita M.",
      Age: "30",
      Project: "WS",
      Team: ["TST", "PUB"],
      State: "SK"
    },
    {
      Name: "Juraj H.",
      Age: "50",
      Project: "WS",
      Team: ["DEV", "PUB"],
      State: "SK",
      Extra: "approxAge"
    },
    {
      Name: "Stefan .",
      Age: "34",
      Project: "None",
      Team: ["HR", "MNGMNT"],
      State: "SK",
      Extra: "approxAge"
    }
  ];
  const handleSelect = (value) => {
    setSelectedOption(null);
    const selectedColleague = dataset.find(
        (colleague) => colleague.Name === value
    );
    setSelectedColleagues([...selectedColleagues, selectedColleague]);
    setNumTries((prev) => {
      const tries = prev + 1;

      if (value === mysteryColleague.Name) {
        setGuessed(true);
        saveStats(true);
        setResultModalVisible(true);
        setShowPlayAgain(true);
        clearSavedGame(); // clear game
      }
      else if(!guessed && tries >= maxGuesses) {
        setFailed(true);
        saveStats(false);
        setResultModalVisible(true);
        setShowPlayAgain(true);
        clearSavedGame(); // clear game
      }
      return tries;
    });
  };

  useEffect(() => {
    // Load the game state from localStorage on component mount
    const savedGame = localStorage.getItem('gameState');
    if (savedGame) {
      loadSavedGame(savedGame);
    }
    else {
      getMysteryColleague();
    }
  }, []);


  useEffect(() => {
    const savedStats = localStorage.getItem("gameStats");
    if (savedStats) {
      setStats(JSON.parse(savedStats));
    }
  }, []);

  useEffect(() => {
    if(selectedColleagues.length > 0 && !(guessed || failed)){
      saveGame()
    }
  }, [guessed, failed, selectedColleagues]);

  const options = dataset
      .filter(
          (colleague) =>
              !selectedColleagues.some((selected) => selected.Name === colleague.Name)
      )
      .map((colleague) => ({
        value: colleague.Name,
        label: (
            <div>
              <strong>{colleague.Name}</strong> - {colleague.Age} {colleague.Extra ? " * " : ""},{" "}
              {colleague.Team.join(" / ")},{" "}{colleague.State}
            </div>
        )
      }));

  const saveStats = (guessedCorrectly) => {
    const newStats = [...stats];
    newStats.push({
      guessedCorrectly,
      numTries: numTries + 1,
      maxGuesses,
      mysteryColleague: mysteryColleague,
      timestamp:
          new Date().toLocaleDateString() + " " + new Date().toLocaleTimeString()
    });
    setStats(newStats);
    localStorage.setItem("gameStats", JSON.stringify(newStats));
  };

  const loadSavedGame = (savedGame) =>{
    console.log('restored old colleague');
    const gameState = JSON.parse(savedGame);
    setSelectedColleagues(gameState.selectedColleagues);
    setNumTries(gameState.numTries);
    setMysteryColleague(gameState.mysteryColleague);
  }
  const getMysteryColleague = () => {
    const randomIndex = Math.floor(Math.random() * dataset.length);
    const mysteryColleague1 = dataset[randomIndex];
    setMysteryColleague(mysteryColleague1);
  }

  const saveGame = () => {
    const gameState = {
      selectedColleagues,
      numTries,
      mysteryColleague: mysteryColleague,
    };
    localStorage.setItem('gameState', JSON.stringify(gameState));
  };

  const clearSavedGame = () => {
    localStorage.removeItem('gameState');
  };

  const playAgain = () => {
    setShowPlayAgain(false);
    setSelectedColleagues([]);
    setNumTries(0);
    setGuessed(false);
    setFailed(false);
    getMysteryColleague();
  }

  const handleHelpModalOpen = () => {
    setHelpModalVisible(true);
  };

  const handleHelpModalClose = () => {
    setHelpModalVisible(false);
  };

  const handleStatsModalOpen = () => {
    setStatsModalVisible(true);
  };

  const handleStatsModalClose = () => {
    setStatsModalVisible(false);
  };

  const handleResultModalClose = () => {
    setResultModalVisible(false);
  };

  const guessTableColumns = [
    {
      title: "Name",
      dataIndex: "Name",
      key: "Name",
      render: (text, record) => {
        if (record.Name === mysteryColleague.Name) {
          return (
              <span style={{ color: "green", fontWeight: "bold" }}>{text}</span>
          );
        }
        return text;
      }
    },
    {
      title: "Age",
      dataIndex: "Age",
      key: "Age",
      render: (text, record) => {
        const ageDifference = record.Age - mysteryColleague.Age;
        let symbol = "";
        let hint = "";
        if (ageDifference < 0) {
          symbol = "";
          hint = "Close, but Mystery colleague is older.";
        } else if (ageDifference > 0) {
          symbol = "";
          hint = "Close, but Mystery colleague is younger.";
        }

        if (record.Age === mysteryColleague.Age) {
          return (
              <span style={{ color: "green", fontWeight: "bold" }}>{text}</span>
          );
        } else if (Math.abs(record.Age - mysteryColleague.Age) <= 3) {
          return (
              <span title={hint} style={{ color: "orange", fontWeight: "bold" }}>
              {text + " " + symbol}
            </span>
          );
        }
        return text + symbol;
      }
    },
    {
      title: "Project",
      dataIndex: "Project",
      key: "Project",
      render: (text, record) => {
        if (record.Project === mysteryColleague.Project) {
          return (
              <span style={{ color: "green", fontWeight: "bold" }}>{text}</span>
          );
        }
        return text;
      }
    },
    {
      title: "Team / Position",
      dataIndex: "Team",
      key: "Team",
      render: (text, record) => {
        if (
            record.Team[0] === mysteryColleague.Team[0] &&
            record.Team[1] === mysteryColleague.Team[1]
        ) {
          return (
              <span style={{ color: "green", fontWeight: "bold" }}>
              {text[0] + " / " + text[1]}
            </span>
          );
        } else if (
            record.Team[0] === mysteryColleague.Team[0] ||
            record.Team[1] === mysteryColleague.Team[1]
        ) {
          return (
              <span
                  title="Close, one of it is correct"
                  style={{ color: "orange", fontWeight: "bold" }}
              >
              {text[0] + " / " + text[1]}
            </span>
          );
        } else {
          return text[0] + " /" + text[1];
        }
      }
    },
    {
      title: "State",
      dataIndex: "State",
      key: "State",
      render: (text, record) => {
        if (record.State === mysteryColleague.State) {
          return (
              <span style={{ color: "green", fontWeight: "bold" }}>{text}</span>
          );
        }
        return text;
      }
    }
  ];

  return (
      <div
          style={{
            fontFamily: "TT Norms Pro, sans-serif",
            flexDirection: "column",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            height: "100vh"
          }}
      >
        <h1 style={{ textTransform: "uppercase", fontWeight: "300" }}>
          Guess the mystery colleague
        </h1>
        {!(guessed || failed) && (
            <AutoComplete
                style={{ textAlign: "center", width: "350px" }}
                options={options}
                onSelect={handleSelect}
                placeholder="Enter a colleague's name"
                value={selectedOption}
            />
        )}
        {showPlayAgain && <Button onClick={playAgain}>Play Again</Button>}
        {selectedColleagues.length > 0 && (
            <Table
                dataSource={selectedColleagues}
                columns={guessTableColumns}
                pagination={false}
                style={{ marginTop: "16px" }}
                rowKey={ (record,index) => index}
            />
        )}
        <GuessCounter maxGuesses={maxGuesses} numTries={numTries} />
        <div
            style={{
              marginTop: "16px",
              display: "flex",
              alignItems: "center"
            }}
        >
          <Button type="text" onClick={handleHelpModalOpen}>
            How To Play
          </Button>
          {" - "}
          <Button type="text" onClick={handleStatsModalOpen}>
            Stats
          </Button>
        </div>
        <HelpModal maxGuesses={maxGuesses} isOpen={helpModalVisible} onClose={handleHelpModalClose}/>
        {guessed && (
            <Modal
                open={resultModalVisible}
                onCancel={handleResultModalClose}
                footer={[
                  <Button key="close" onClick={handleResultModalClose}>
                    Close
                  </Button>
                ]}
            >
              <div
                  style={{
                    fontFamily: "TT Norms Pro, sans-serif",
                    flexDirection: "column",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    textAlign: "center"
                  }}
              >
                <h1 style={{ justifyContent: "center" , color: "green"}}>
                  WELL
                  <br />
                  DONE!
                </h1>
                <p>
                  You got today's Mystery colleague with{" "}
                  <span style={{ fontWeight: "bold", color: "green" }}>
                {numTries}
              </span>{" "}
                  tries!{" "}
                </p>
                {/*<p>TODO: You are on a n day streak!</p>*/}
                <p>
                  You correctly guessed the colleague:{" "}
                  <span style={{ fontWeight: "bold" }}>{mysteryColleague.Name}</span>
                </p>
              {/*<p>New Mystery colleague will be ready to guess in:</p>
                <Countdown/>*/}
              </div>
            </Modal>
        )}
        {!guessed && failed && (
            <Modal
                open={resultModalVisible}
                onCancel={handleResultModalClose}
                footer={[
                  <Button key="close" onClick={handleResultModalClose}>
                    Close
                  </Button>
                ]}
            >
              <div
                  style={{
                    fontFamily: "TT Norms Pro, sans-serif",
                    flexDirection: "column",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    textAlign: "center"
                  }}
              >
                <h1 style={{color:'red'}}>
                  TOO
                  <br />
                  BAD!
                </h1>
                <p>
                  Sorry, you were unable to guess the colleague within the allowed
                  tries.
                </p>
                <p>
                  Today's Mystery colleague is:{" "}
                  <span style={{ fontWeight: "bold" }}>{mysteryColleague.Name}</span>
                </p>
                {/*<p>New Mystery colleague will be ready to guess in:</p>
                <Countdown/>*/}
              </div>
            </Modal>
        )}
        <StatsModal stats={stats} isOpen={statsModalVisible} onClose={handleStatsModalClose}/>
      </div>
  );
};

export default App;
